@import url(https://fonts.googleapis.com/css?family=Open+Sans|Oswald&display=swap);
/*
font-family: 'Oswald', sans-serif;
font-family: 'Open Sans', sans-serif; 
*/
body, ul, div, h1,h2,h3,h4,h5,p{
    margin: 0px;
    padding: 0px;
}

h1{
  font-size: 98px;
  color: #fff;
  font-family: 'Oswald', sans-serif;
  font-weight: 100;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

h3{
  color: #fff;
  font-family: 'Oswald', sans-serif;
  font-size: 24px;
  font-weight: 100;

}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


header{    
    background-image: url(/boldreact/static/media/hero-home.1053dab8.jpg); 
     background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

header .top-header{
    display: flex; 
    padding: 22px 29px 22px 29px;
    /* background-image: linear-gradient(to top, rgba(255,255,255,0), rgba(255,255,255,0.3));  */
    background: transparent linear-gradient(180deg, #00000080 0%, #00000000 100%) 0% 0% no-repeat padding-box;
}
header .top-header .main-logo{
    width: 176px;
    
}
header .top-header nav{
    margin-left: 230px;
    position: fixed;
}
header .top-header nav .main-menu .main-menu-item{
    width: 62px;
    color: #fff;
    font-family: 'Oswald', sans-serif;
    font-size: 16px;
    list-style: none;
    display: inline-block;
    margin-right: 50px;
}

.sub-menu{
    width: 600px;
    position: absolute;
    padding: 10px;
    background-color:rgba(22, 87, 103, 0.2) ;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);      
    top: -200px;
    transition: top 0.5s;
    left: 0px;

}

.show-menu{
    top: 50px;    
   
}

.sub-menu .close-icon{
    width: 20px;
    position: absolute;
    right: 10px;
}

.sub-menu li{
    
    list-style: none;
    display: inline-block;
    margin-right:86px;
    font-size: 16px;
    font-family: 'Oswald', sans-serif;
    cursor: default;
    
}

.sub-menu li a{
    color: #fff;
    text-decoration: none;
    display: block;
    margin-left:10px;
    margin-top:5px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    font-size: 14px;
}

.season-switch{    
    margin-left: auto;
}

.season-switch .season-switch-button:nth-child(1){
    margin-right: 10px;
}

.clickable{
    cursor: pointer;
}

.separator-bottom{
    width: 100%;
    height: 100px;
    display: block;
}


.heading{
    display: block;
    margin-top: 31vh;
    width: 100%;
    text-align: right;
    background: transparent linear-gradient(180deg, #00000000 0%, #000000B2 100%) 0% 0% no-repeat padding-box; 
    
}

.heading-div{
    margin-right: 10%;
    margin-left: auto;
    width: 330px;
}
.heading h3{
   text-transform: uppercase;
}

.heading .btn{
    margin-top: 10px;
}

.sidebar-widget{
    position: absolute;
    z-index: 1;
    right: 29px;
    top:123px;
    display: flex;
    overflow: hidden;
}

.sidebar-widget .widget-weather{
    background-color: rgba(22, 87, 103, 0.4);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    text-align: center;
    width: 239px;
    padding-top: 40px;
    position: relative;
    right: -300px;
    transition: right 0.2s;
}

.sidebar-widget  .show-widget{
    right: 0px;
    transition: right 0.2s;
}

.widget-weather .date-weather{
    color: #fff;
    font-family: "Oswald", sans-serif;
    font-size: 16px;
}

.widget-weather .secondary-text{
    width: 116px;
    text-align: center;
    display: block;
    margin: auto;
    font-size: 14px;
}

.widget-weather .weather-icon{
    margin-top: 30px;
    width: auto;
    margin-bottom: 10px;
}

.widget-weather .surface-title{
    font-size: 24px;
    color: #fff;
    font-family: "Oswald", sans-serif;
}

.widget-weather .box-separator{
    margin-top: 20px;
    margin-bottom: 20px;
}
.widget-weather .btn-widget{
    border: 1px solid #fff;
    background-color: rgba(0,0,0,0);
    margin: auto;
}


.sidebar-widget .widget-box{
    padding-bottom: 10px;
    text-align: center;
}


.box-separator{
    width: 30px;   
    background-color: #fff;
    height:  1px;
    margin: auto;
    display: block;
    margin-top: 10px;
    margin-bottom: 10px;
}

#plusSidebarWidget{
    width: 30px;
    display: block;
    margin: auto;
    cursor: pointer;
}

.sidebar-widget h3{
    font-family: 'Oswald', sans-serif;
    font-weight: 100;
    font-size: 32px;
}

.sidebar-widget h5, .secondary-text{
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    text-transform: uppercase;
}

.btn{
    padding: 9px 19px 9px 19px;
    font-weight: 100;
    font-size: 16px;
    color: #fff;
    text-transform: uppercase;
    font-family: 'Open Sans', sans-serif;
    display: block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    cursor: pointer;
    
}

.btn-primary{
    background-color: #0C97B9;
}

@media only screen and (max-width:980px){

    .top-header nav{
        display: none;
        background: red;
    }
    header .top-header .main-logo{
        width: 108px;
        
    }
    h1{
        font-size: 80px;
    }
    h3{
        font-size: 20px;
    }
    .heading{
        margin-top: 50%;
    }

    .heading-div{
        margin-right: 30px;
    }
   
   

   .mobile-active{
    background-color: rgba(22, 87, 103, 0.4);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    margin-right: 12px;
    padding-right: 12px;
    right: 0px;
    padding-bottom: 10px;
    
   }
   .mobile-active .widget-weather{
    background-color: transparent;
    background-color: initial;
    -webkit-backdrop-filter: initial;
            backdrop-filter: initial;
   }
    .hide-mobile.hide{
        display: none;
    }

}


h1 h2 h3 h4{
    margin:0px;
    padding:0px;
}

.booking-bar{
    background-color: #fff;
    width: 100%;
    display: flex;
    margin-top: -2px;

}

.left-side{
    width: 50%;
    border-right: 1px solid;
    border-color: #0C97B9;
    margin-top: 67px;
    padding-bottom: 40px;
}

.booking-title{
    color:#B90CA5;
    font-size: 98px;
    font-family: 'Oswald', sans-serif;
    width: auto;
}

.booking-subtitle{
    color:#165767;
    font-size: 24px;
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    float: right;
    margin-right: 70px;
    width: auto;
}


.right-side{
    width: 50%;
    padding-top: 126px;
}


.right-side input{
    width: 210px;
    border: #E2E2E2 2px solid;
    margin-right: 20px;
    display: inline-block;
    font-family: 'Open Sans', sans-serif;
    padding: 15px;
}

.right-side input:nth-child(3),.right-side input:nth-child(4) {
    margin-top: 10px;
}
.booking-button{
    background-color: #0C97B9;
    font-size: 16px;
    padding: 15px 65px;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    margin-right: 20px;
    width: 210px;
    height: 52px;
}
