h1 h2 h3 h4{
    margin:0px;
    padding:0px;
}

.booking-bar{
    background-color: #fff;
    width: 100%;
    display: flex;
    margin-top: -2px;

}

.left-side{
    width: 50%;
    border-right: 1px solid;
    border-color: #0C97B9;
    margin-top: 67px;
    padding-bottom: 40px;
}

.booking-title{
    color:#B90CA5;
    font-size: 98px;
    font-family: 'Oswald', sans-serif;
    width: auto;
}

.booking-subtitle{
    color:#165767;
    font-size: 24px;
    font-family: 'Oswald', sans-serif;
    text-transform: uppercase;
    float: right;
    margin-right: 70px;
    width: auto;
}


.right-side{
    width: 50%;
    padding-top: 126px;
}


.right-side input{
    width: 210px;
    border: #E2E2E2 2px solid;
    margin-right: 20px;
    display: inline-block;
    font-family: 'Open Sans', sans-serif;
    padding: 15px;
}

.right-side input:nth-child(3),.right-side input:nth-child(4) {
    margin-top: 10px;
}
.booking-button{
    background-color: #0C97B9;
    font-size: 16px;
    padding: 15px 65px;
    color: #fff;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    margin-right: 20px;
    width: 210px;
    height: 52px;
}